import React, { Component } from 'react';
import './NewInstances.css';
import {Link} from "react-router-dom";
import sword from '../../img/sword.png';
import whiteArrow from '../../img/whiteArrow.png'

class NewInstances extends Component{

    onRunningInstance =() =>{
   
        window.location= "/LiveInstancesLayout";
    }

    onRunningZenAnalytica=() =>{
   
        window.location.href= "/ZenAnalyticaDashboardLayout";
    }
    render(){
     
        return (
            <div >
                <br/>
                 <div className='whitebox'>
                    <div className='coloredBox' >
                        <div className="insideColoredBox" style={{textAlign:'center',fontFamily:'Helvetica'}}>Virtual ITSM Assistance</div>
                        <div className="more">
                        <Link to="/LibraryLayout" style={{color:"white",fontSize:"18px"}}>more
                            <img src={whiteArrow} alt="arrow"/>
                        </Link>
                        </div>
                    </div>
                    <div >    
                        <div className="containerServices" >
                            <div className="services"  >
                                <span className="services1" >ZEVA</span>
                                <span className="services1">ZA</span>
                            </div>   
                            <div className="services" >
                                <span className="services1" >ZEVA</span>
                                <span className="services1" >ZA</span>
                            </div >   
                            <div className="services" >
                                <span className="services1">ZEVA</span>
                                <span className="services1"> <img src={sword} alt="Samurai" /></span>
                                <span className="services1">ZA</span>
                            </div>  
                            <div className="services" >
                                <span className="services1">ZEVA</span>
                            </div>
                        </div> 
                        <div className="containerLine" >
                            <div id="first-tick-mark" ></div>
                            <div id="next-tick-mark" ></div>
                            <div id="next-tick-mark" ></div>                           
                            <div id="next-tick-mark" onClick={this.onRunningInstance} ></div> 
                        </div> 
                        <div className="containerLabel" >
                            <span>Onboarding</span>
                            <span>Configuration</span>
                            <span>Integration</span>
                            <span>Live</span>
                        </div>  
                    </div> 
                </div>
                <br/>
                <br/>
                <div className='whitebox'>
                    <div className='coloredBox' >
                        <div className="insideColoredBox" style={{textAlign:'center',fontFamily:'Helvetica'}}>ZenAnalytica Dashboard</div>
                        <div className="more">
                        <Link to="/LibraryLayout" style={{color:"white",fontSize:"18px"}}>more
                            <img src={whiteArrow} alt="arrow"/>
                        </Link>
                        </div>
                    </div>
                    <div >    
                        <div className="containerServicesofZenAnalytica" >
                            <div className="services"  >
                                <span className="services1">ZA</span>
                                
                            </div>   
                            <div className="services" >
                                <span className="services1" >ZA</span>
                            </div >   
                            <div className="services" >
                                <span className="services1">ZA</span>
                                <span className="services1"> <img src={sword} alt="Samurai" /></span>
                            </div>  
                            <div className="services" >
                                <span className="services1">ZEVA</span>
                            </div>
                        </div> 
                        <div className="containerLine" >
                            <div id="first-tick-mark" ></div>
                            <div id="next-tick-mark" ></div>
                            <div id="next-tick-mark" ></div>                           
                            <div id="next-tick-mark" onClick={this.onRunningInstance} ></div> 
                        </div> 
                        <div className="containerLabel" >
                            <span>Onboarding</span>
                            <span>Configuration</span>
                            <span>Integration</span>
                            <span>Live</span>
                        </div>  
                    </div> 
                </div>
               
            </div>
         
            );
        }


}

export default NewInstances;