import React, { Component } from 'react';
import './Toolbar.css';
import DashboardImg from '../img/Dashboard.png';
import LibraryImg from '../img/Library.png';
import NotificationImg from '../img/Notification.png';
import UserImg from '../img/User.png';
import ToggleButton from '../img/toggleButton.png';
import LogoutImg from '../img/Icon ionic-ios-exit.svg';
import {Link} from "react-router-dom";


class Toolbar extends Component{
    render(){
      
        return (
            <div>
                <div className="toolbar">
                    <nav className="toolbar_navigation">
                        <div className="toolbar_navigation_items">
                            <ul>
                                <br/>   
                                <Link to="/DashboardLayout">  
                                        <img className="toggleButton" src={ToggleButton} alt="ToggleButton" title="ToggleButton"/>
                                </Link>  

                                <Link to="/DashboardLayout">  
                                    <h6 className="DashboardIcon" >
                                        <img className= "DashboardImg" src={DashboardImg} alt="Dashboard" title="Dashboard"/>
                                        <br/><span className="iconName">Dashboard</span>
                                    </h6>
                                </Link>  
                              
                                <Link to="/LibraryLayout">  
                                    <h6 className="LibraryIcon" >
                                        <img className="LibraryImg" src={LibraryImg} alt="Library" title= "Library"/>
                                        <br/><span className="iconName">Service Library</span>
                                     </h6>
                                </Link>  
                               
                                <Link to="/DashboardLayout">  
                                    <h6 className="NotificationIcon" >
                                        <img className="NotificationImg" src={NotificationImg} alt="Notification" title="Notification"/>
                                        <br/><span className="iconName">Notification</span>
                                     </h6>
                                </Link>     
                                
                                <Link to="/DashboardLayout">  
                                    <h6 className="UserIcon" style={{color: 'white',position: "absolute",top:"462px",alignSelf:"center",float: "none",left:"25%"}}>
                                        <img className="UserImg" src={UserImg} alt="User" title="User Profile" style={{height:"40px", width:"40px" }}/>
                                        <br/><span className="iconName">Admin</span>
                                     </h6>
                                </Link>   
                               
                                
                            </ul>
                        </div>
                    </nav>
                </div>
                <div className ="Logout">
                    <Link to="/Logout">  
                        <img src={LogoutImg} alt="logout" title="Logout" style={{ position: "absolute",height:"44px", width:"57px",alignSelf:"center"}} />
                    </Link> 
                </div>
            </div>
        );
        }


}

export default Toolbar;