import React, { Component } from 'react';
import './Dashboard.css';
import NewInstances from './NewInstances/NewInstances';
import RunningInstance from '../RunningInstanceLayout/RunningInstance';


class Dashboard extends Component{

    openNewInstance=()=>{
        if (document.getElementById("newInstance")) {
            document.getElementById("newInstance").style.display = "block";
            document.getElementById("runningInstance").style.display = "none";
          }
    }

    openRunningInstance=()=>
    {
        if (document.getElementById("runningInstance")) {
            document.getElementById("newInstance").style.display = "none";
            document.getElementById("runningInstance").style.display = "block";
          }
    }
    render(){
      
        return (
            <div>
            <div>
                <div >
                    <div className="logoImg"/>
                    <div className="title">
                        <span className="titleName">DASHBOARD</span><br/>
                        <span className="titleDescription">Navigate through running and new instances</span>
                    </div>
                </div>
            <br/>
            <br/>
            <br/>
            </div>
            <br/>
                <div className="dividerLine" >
                    <div className="rectangelBox" onClick={this.openRunningInstance}>
                        <span className="boldTab">Running </span>
                        <span className="thinTab">Instances</span>
                    </div>
                    <div className="vl"></div>
                    <div className="rectangelBox" onClick={this.openNewInstance}>
                        <span className="boldTab"  style={{width:"43px"}}>New</span>
                        <span className="thinTab">Instances</span>
                    </div>
                </div>
                <br/>
                <br/>
                <br/>
                <br/>
                <div className="newInstance" id="newInstance" style={{display:"none"}}><NewInstances/></div>
                <div className="runningInstance" id="runningInstance" style={{display:"block"}}><RunningInstance/></div>
            </div>
       
        );
        }


}

export default Dashboard;